import { css, keyframes } from 'styled-components';

const chalkUnderlineAnimation = keyframes`
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
`

export const chalkUnderlineStyle = css`
  position: relative;
  cursor: url(${process.env.ASSET_PREFIX}/chalk-stick.svg), auto;

  &:after {
    content: '';
    display: block;
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 0%;
    height: 4px;
    background-image: url(${process.env.ASSET_PREFIX}/chalk-line.png);
    background-repeat: no-repeat;
    background-size: 100%;
    animation: 0.5s ${chalkUnderlineAnimation} linear forwards;
  }
`;

export const screenReaderOnly = css`
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
`;
